		var oneMonthAgo = new Date(date.setMonth(date.getMonth() - 1));
		var oneMonthyear = oneMonthAgo.getFullYear();
		var oneMonthmonth = (oneMonthAgo.getMonth() + 1);
		var oneMonthday = oneMonthAgo.getDate();

