		onClickClientOrderRow(event, data){
			var sum_count =0;
			for(var x=0; x<this.ReleaseCountItems.length;x++){
				if(data.item.clm_client_order_id==this.ReleaseCountItems[x].clm_stock_client_order_id && data.item.clm_seq==this.ReleaseCountItems[x].clm_stock_client_order_seq){
					sum_count =sum_count +Number(this.ReleaseCountItems[x].clm_release_count);
				}
			}
			var left_count =Number(data.item.clm_left_order_count)-sum_count;
			if(this.ReleaseCountItems[this.tmp_index].clm_stock_client_order_id == data.item.clm_client_order_id){
				//같은번호 눌렀을때
				this.clientOrderModal = false;
				return;
			}else if(left_count==0){
				this.ReleaseCountItems[this.tmp_index].clm_release_count=left_count;
				//가용 수량 없을때
				alert(data.item.clm_client_order_id+'의 주문 수량을 초과했습니다.');
				this.clientOrderModal = false;
				return;
			}else if(left_count<0){
				//가용 수량 없을때
				alert(data.item.clm_client_order_id+'의 주문 수량을 초과했습니다.');
				this.clientOrderModal = false;
				return;
			}else{
				this.ReleaseCountItems[this.tmp_index].clm_release_client_order_count = Number(left_count);
				// this.ReleaseCountItems[this.tmp_index].clm_before_release_count = Number(left_count);
				if(left_count >= Number(this.ReleaseCountItems[this.tmp_index].clm_release_count)){
					//주문수량 >= 출하 수량
				}else{
					//주문수량 < 출하 수량
					this.ReleaseCountItems.splice(this.tmp_index+1, 0,{
						clm_release_id:this.ReleaseCountItems[this.tmp_index].clm_release_id,
						clm_release_seq :this.ReleaseCountItems[this.tmp_index].clm_release_seq,
						lotno :this.ReleaseCountItems[this.tmp_index].lotno,
						clm_product_id : this.ReleaseCountItems[this.tmp_index].clm_product_id,
						clm_product_name :this.ReleaseCountItems[this.tmp_index].clm_product_name,
						clm_original_release_count:this.ReleaseCountItems[this.tmp_index].clm_original_release_count,
						clm_release_enable_count :this.ReleaseCountItems[this.tmp_index].clm_release_enable_count,
						clm_release_count :Number(this.ReleaseCountItems[this.tmp_index].clm_release_count)-Number(left_count),
						clm_before_release_count :Number(this.ReleaseCountItems[this.tmp_index].clm_release_count)-Number(left_count),
						clm_release_client_order_count :0,
						clm_release_client_id :'',
						clm_client_id:this.ReleaseCountItems[this.tmp_index].clm_client_id,
						clm_client_name :'',
						clm_joborder_id :this.ReleaseCountItems[this.tmp_index].clm_joborder_id,
						clm_client_order_id :this.ReleaseCountItems[this.tmp_index].clm_client_order_id,
						clm_stock_client_order_id :'',
						clm_stock_client_order_seq :'',
						clm_gubun : this.ReleaseCountItems[this.tmp_index].clm_gubun,
						clm_material_order_id:this.ReleaseCountItems[this.tmp_index].clm_material_order_id,
						clm_order_income_seq:this.ReleaseCountItems[this.tmp_index].clm_order_income_seq,
