						clm_material_leftover_seq:this.ReleaseCountItems[this.tmp_index].clm_material_leftover_seq,
					});
					this.ReleaseCountItems[this.tmp_index].clm_release_count=left_count;
					this.ReleaseCountItems[this.tmp_index].clm_before_release_count=left_count;
				}
