		getPaymentModalList(){
			const vm = this
			this.loading2 = true
			let items = axios.get('/back/Popup/StockAllPayment',{
				params: {
			        search: encodeURI(this.search)
					}
				}).then(
				response => {
					let items = response.data	
					vm.loading2 = false
					for(var x=0;x<items.length;x++ ) {
						if(x !=0 && items[x-1].clm_request_delivery_datetime == items[x].clm_request_delivery_datetime) { 
							// 이전 행의 데이터값과 기존 행의 데이터값이 같을 때 rowspan 값은 1이 되어야 함 (그래야 추가 병합이 일어나지 않음)
							items[x].rowspan = 'y'
						}
						if(x !=0 && items[x-1].clm_client_name == items[x].clm_client_name&& items[x-1].clm_request_delivery_datetime == items[x].clm_request_delivery_datetime) { 
							items[x].clientspan = 'y'
						}
						
					}
					this.m_payment_items = items
					console.log(items)
				}
			)
		},
		getList() {
			this.selected_items=[];
			const vm = this
			this.loading = true		
			// this.save_yn='Y';	
			return new Promise((resolve, reject) => {
				const { sortBy, sortDesc, page, itemsPerPage } = this.options
				let items = axios.get('/back/MOB001003/List',{
						params: {
							keyword: this.keyword,
							start_date: this.start_date,
							end_date: this.end_date,
							mob:'mob001',
							gubun_id:this.gubun_id,
							clm_user: this.$session.get('user_id'),
							page: page,
							PerPage: itemsPerPage
						}
					}).then(
					response => {
						if (response.status == '200') {
							items = response.data
							setTimeout(() => {
								this.loading = false
								this.save_yn='N';
								this.document = items
							}, 500)
						}else{
							this.save_yn='N';
							this.loading = false
							Swal.fire({
								title:'서버가 불안정합니다.\n다시 조회해주세요.',
								icon: 'error'
							});
							this.document =[];
						}
					},
				)
			})
			this.selected_items = [];
		},
