		WorkEndComplete(){
			if(this.loading_yn=='Y'){
				alert('저장중입니다.잠시만 기다려주세요.');
				return;
			}
			this.loading_yn='Y';
			
			if(this.clm_ply_yn=='Y'){
				alert('합판공정은 생산계획 화면에서만 작업완료가 가능합니다.');
				this.loading_yn='N';
				return;
			}
			var end_yn='N';
			if(this.m2_half_poor_count=='' || typeof this.m2_half_poor_count=='undefiend'){
				this.m2_half_poor_count=0;
			}
			if(this.m2_half_produce_count=='' || Number(this.m2_half_produce_count)==0){
				alert('작업 완료 수량을 입력해주시기 바랍니다.');
				this.loading_yn='N';
				return;
			}else if(Number(this.m2_half_produce_count)<0 || Number(this.m2_half_produce_count)% 1!= 0){
				alert('작업 완료 수량이 음수 또는 소수가 될 수 없습니다.');
				this.loading_yn='N';
				return;
			}else if(Number(this.m2_half_usable_count)<Number(this.m2_half_produce_count)){
				alert('작업 완료 수량이 작업 미완료 수량을 초과했습니다.');
				this.loading_yn='N';
				return;
			}else if(Number(this.m2_half_poor_count)<0 || Number(this.m2_half_poor_count)% 1!= 0){
				alert('불량 수량이 음수 또는 소수가 될 수 없습니다.');
				this.loading_yn='N';
				return;
			}else if(Number(this.m2_half_usable_count)<Number(this.m2_half_poor_count)){
				alert('불량 수량이 작업 미완료 수량을 초과했습니다.');
				this.loading_yn='N';
				return;
			}else if(Number(this.m2_half_usable_count)<Number(this.m2_half_poor_count)+Number(this.m2_half_produce_count)){
				alert('작업 완료 수량과 불량 수량의 합이 작업 미완료 수량을 초과했습니다.');
				this.loading_yn='N';
				return;
			}
			if(Number(this.m2_half_usable_count)==Number(this.m2_half_produce_count)+Number(this.m2_half_poor_count)){
				end_yn='Y';
			}
			axios.get('/back/MOB001003/SelectCompleteCount',{
				params: {
					clm_half_joborder_id: this.selected_items[0].clm_half_joborder_id,
					clm_half_option_id: this.selected_items[0].clm_half_option_id
				}
			}).then(
			response => {
				if (response.status == '200') {	
					if(Number(this.m2_half_produce_count)>Number(response.data)){
						alert('이미 완료된 작업입니다.');
						this.loading_yn='N';
						this.WorkEndModal = false;
						this.getList();
					}else{
						axios.post('/back/MOB001003/WorkEnd', null, { params: {
							half_joborder_id: this.m2_half_joborder_id,
							clm_half_joborder_id: this.selected_items[0].clm_half_joborder_id,
							clm_half_option_id: this.selected_items[0].clm_half_option_id,
							clm_half_id :this.selected_items[0].clm_half_id,
							produce_count:Number(this.m2_half_produce_count),
							poor_count:Number(this.m2_half_poor_count),
							end_yn:end_yn,
							half_joborder_user:this.m2_half_joborder_user,
							material_id:this.selected_items[0].clm_material_id,
							material_order_id: this.selected_items[0].clm_material_order_id,
							order_income_seq: this.selected_items[0].clm_order_income_seq,
							income_material_seq: this.selected_items[0].clm_income_material_seq,
							material_leftover_seq: this.selected_items[0].clm_material_leftover_seq,
							material_use_count:this.selected_items[0].clm_material_use_count,
							material_full_count: String(Number(this.selected_items[0].clm_usable_count)),
							material_remainder_count:  String(Number(this.selected_items[0].clm_usable_count) - Number(this.selected_items[0].clm_material_use_count)),
							user_id: this.user_id
						}})
						.then(response => {
							if (response.status == '200') {	
								this.loading_yn='N';					
								this.WorkEndModal=false;
								if(response.data=='Y'){
									Swal.fire({
										title:"정상적으로 등록되었습니다.\n후공정을 등록하시겠습니까?",
										text:  '',
										icon: 'question',
										showCancelButton: true,
										allowOutsideClick: false,
										confirmButtonColor: '#3085d6',
										cancelButtonColor: '#d33',
										confirmButtonText: '예',
										cancelButtonText: '아니요'
									}).then((result) => {
										if (result.isConfirmed) {
											var poor =0;
											axios.get('/back/MOB001003/SelectTotalPoorCount',{
												params: {
												clm_half_joborder_id: this.m2_half_joborder_id,
											}})
											.then(response => {
												if (response.status == '200') {	
													if(Number(response.data)>0){
														poor = Number(response.data);
													}
													this.clm_m6_material_count= Number(this.m2_half_total_count) -poor;
													this.clm_m6_material_full_count= Number(this.m2_half_total_count)  -poor;
													this.OpenMaterialAfter();
												}
											});
										}else{
											if(Number(this.m2_half_usable_count)==Number(this.m2_half_produce_count)){
												alert('이전에 후공정을 등록하였으면 LotNo가 바뀔 수 있습니다.');
												this.CreateLeftOver();
												this.getList();
											}else{
												this.getList();
												this.selected_items=[];
												// this.selected_items=[];
											}
										}
									})
									/*
									if(confirm('정상적으로 등록되었습니다.\n후공정을 등록하시겠습니까?')){
										var poor =0;
