												this.clm_m6_material_full_count= Number(this.m2_half_total_count)  -poor;
												this.OpenMaterialAfter();
											}
										});
										
									}else{
										if(Number(this.m2_half_usable_count)==Number(this.m2_half_produce_count)){
											alert('이전에 후공정을 등록하였으면 LotNo가 바뀔 수 있습니다.');
											this.CreateLeftOver();
											this.getList();
										}else{
											this.getList();
											this.selected_items=[];
											// this.selected_items=[];
										}
									}*/
								}else{
									Swal.fire({
										title: '정상적으로 등록 되었습니다.',
										icon: 'success'
									});
									this.getList();
									this.selected_items=[];
								}
							}
						});
					}
				}
			});
		},
