				return;
			}
			this.loading_yn='Y';
			axios.post('/back/MOB001003/LeftOverCheck', null, { params: {
				clm_material_id:this.selected_items[0].clm_half_id,
				clm_material_order_id: this.selected_items[0].clm_half_order_id,
				clm_order_income_seq: this.selected_items[0].clm_half_income_seq,
				clm_income_material_seq: this.selected_items[0].clm_half_material_seq,
				clm_material_leftover_seq:'0',
				clm_material_use_count:this.selected_items[0].clm_half_product_count,
				user_id: this.user_id
			}})
