					.catch(error => {
						console.log(error)
					})
				}
			}
			if(error200=='Y'){
				alert('저장 중 오류가 발생했습니다.');
				this.selected_items=[];
			}else{
				Swal.fire({
					title: '정상적으로 저장 되었습니다.',
					icon: 'success'
				});
				this.loading_yn='N';
				this.getList();
				this.PostProcessModal=false;
				this.selected_items=[];
				this.selected_items2=[];
				this.selected_items2=[];
			}
		},
		changeAfter(cnt){
			if(cnt==1 && this.clm_m6_preservatives_yn=='N'){
				this.clm_m6_user_name1='';
				this.clm_m6_user_id1='';
			}else if(cnt==2 && this.clm_m6_marking_yn=='N'){
				this.clm_m6_user_name2='';
				this.clm_m6_user_id2='';
			}else if(cnt==3 && this.clm_m6_wrapping_yn=='N'){
				this.clm_m6_user_name3='';
				this.clm_m6_user_id3='';
			}else if(cnt==4 && this.clm_m6_heat_yn=='N'){
				this.clm_m6_user_name4='';
				this.clm_m6_user_id4='';
			}else if(cnt==5 && this.clm_m6_bending_yn=='N'){
				this.clm_m6_user_name5='';
				this.clm_m6_user_id5='';
			}else if(cnt==6 && this.clm_m6_air_yn=='N'){
				this.clm_m6_user_name6='';
				this.clm_m6_user_id6='';
			}else if(cnt==7 && this.clm_m6_paint_yn=='N'){
				this.clm_m6_user_name7='';
				this.clm_m6_user_id7='';
			}else if(cnt==8 && this.clm_m6_grinder_yn=='N'){
				this.clm_m6_user_name8='';
				this.clm_m6_user_id8='';
			}
		},
		workEnd(){
			var error = 'N';
			var half_joborder_id = '';
			var end_yn ='N';
			if(this.selected_items.length == 1){
				if(this.selected_items[0].clm_half_joborder_datetime != ''){
					error = 'Y'
				}
				if(half_joborder_id == ''){
					half_joborder_id = this.selected_items[0].clm_half_joborder_id
				}else{
					half_joborder_id = half_joborder_id + ',' + this.selected_items[0].clm_half_joborder_id
				}
			}else{
				error = 'YY';
			}
			if(Number(this.m2_half_usable_count)==Number(this.m2_half_produce_count)){
				end_yn='Y';
			}
			if(error == 'Y'){
				alert('이미 작업완료된 반제품작업이 선택되어 있습니다.');
				return;
			}else if(error == 'YY'){
				alert('선택된 반제품작업이 한개가 아닙니다.');
				return;
			}else{
				axios.post('/back/MOB001003/WorkEnd', null, { params: {
					half_joborder_id: half_joborder_id,
					clm_half_joborder_id: this.selected_items[0].clm_half_joborder_id,
					clm_half_id :this.selected_items[0].clm_half_id,
					produce_count:Number(this.m2_half_produce_count),
					end_yn:end_yn,
					user_id: this.user_id
				}})
				.then(response => {
					this.getList();
					this.selected_items=[];
				});
			}
		},
	}
}
</script>